import React from "react";
import AwesomeSlider from "react-awesome-slider";
import withAutoplay from "react-awesome-slider/dist/autoplay";
import "react-awesome-slider/dist/styles.css";
//import "../styles/galleries.css";

const CodeletGallery = () => {
  const AutoplaySlider1 = withAutoplay(AwesomeSlider);
  return (
    <AutoplaySlider1
      play={true}
      cancelOnInteraction={false} // should stop playing on user interaction
      interval={4000}
    >
      {/* main page image */}
      <div data-src="https://clackurbucket.s3.us-east-2.amazonaws.com/codelet-ss2.png" />
      {/* single post images */}
      <div data-src="https://clackurbucket.s3.us-east-2.amazonaws.com/codelet-ss1.png" />
      <div data-src="https://github.com/b-tsui/Codelet-Frontend/raw/master/documentation/images/card-flip.gif?raw=true" />
      <div data-src="https://github.com/b-tsui/Codelet-Frontend/raw/master/documentation/images/matching-game.gif?raw=true" />
      <div data-src="https://github.com/b-tsui/Codelet-Frontend/raw/master/documentation/images/quiz-game.gif?raw=true" />
    </AutoplaySlider1>
  );
};

export default CodeletGallery;
