import React from "react";
import AwesomeSlider from "react-awesome-slider";
import withAutoplay from "react-awesome-slider/dist/autoplay";
import "react-awesome-slider/dist/styles.css";
import "../styles/galleries.css";

const ClackGallery = () => {
  const AutoplaySlider3 = withAutoplay(AwesomeSlider);
  return (
    <AutoplaySlider3
      play={true}
      cancelOnInteraction={false} // should stop playing on user interaction
      interval={5000}
    >
      <div data-src="https://clackurbucket.s3.us-east-2.amazonaws.com/clackss1.png" />
      <div data-src="https://lkang97.github.io/images/clack.png" />
    </AutoplaySlider3>
  );
};

export default ClackGallery;
